//react router
import { Link } from "react-router-dom";
//scss
import * as S from "./LoginStyles";
import { ILoginUIProps } from "./LoginTypes";
//react-toastify

export default function LoginPresent(props: ILoginUIProps) {
  return (
    <S.LoginWrapper>
      <S.LoginLabel></S.LoginLabel>
      <S.LoginForm>
        <S.LoginInput
          required
          type="text"
          placeholder="username"
          name="username"
          onChange={props.handleChange}
        />
        <S.LoginInput
          required
          type="password"
          placeholder="password"
          name="password"
          onChange={props.handleChange}
        />
        <S.LoginButton onClick={props.handleSubmit}></S.LoginButton>
        {/* {props.err && <p>{props.err}</p>} */}
        <S.LoginSpan>
           ?<Link to="/register"></Link>
        </S.LoginSpan>
      </S.LoginForm>
    </S.LoginWrapper>
  );
}
